body {
  background: #f1f1f1;
  line-height: 1.4;
  font-size: $fz;
  min-width: 320px;
  font-family: 'proximanova', Arial, sans-serif;
  color: $c-text;
  @include no-scroll;
  overflow: hidden;
  white-space: nowrap; }

.icon {
  display: inline-block;
  fill: currentColor;
  font-size: inherit; }

.lazy {
  &.is-loaded:not(.no-animation) {
    animation: fadeIn 1s; } }

.out {
  position: relative;
  z-index: 1;
  // min-height: 100vh
  height: calc(200vh - 17px);
  &--horizontal {
    @include flex;
    .header {
      flex: 0 0 100%; }
    .footer {
      flex: 0 0 100%; }
    .main {
      flex: 0 0 500px; } } }





.canvas-bg {
  position: fixed;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  // overflow: hidden
  z-index: 0;
  canvas {
    // +vcenter
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%; } }

.canvas-grid {
  position: relative;
