body {
  background: #f1f1f1;
  line-height: 1.4;
  font-size: 16px;
  min-width: 320px;
  font-family: 'proximanova', Arial, sans-serif;
  color: #000;
  overflow: hidden;
  white-space: nowrap;
}

body.no-scroll {
  overflow: hidden;
}

.no-touch body.no-scroll {
  margin-right: 17px;
}

.icon {
  display: inline-block;
  fill: currentColor;
  font-size: inherit;
}

.lazy.is-loaded:not(.no-animation) {
  animation: fadeIn 1s;
}

.out {
  position: relative;
  z-index: 1;
  height: calc(200vh - 17px);
}

.out--horizontal {
  display: flex;
  justify-content: flex-start;
  align-items: normal;
  flex-direction: row;
  flex-wrap: nowrap;
}

.out--horizontal .header {
  flex: 0 0 100%;
}

.out--horizontal .footer {
  flex: 0 0 100%;
}

.out--horizontal .main {
  flex: 0 0 500px;
}

.canvas-bg {
  position: fixed;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  z-index: 0;
}

.canvas-bg canvas {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.canvas-grid {
  position: relative;
}

.fadeIn {
  animation-name: fadeIn;
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.overflow-hidden {
  overflow: hidden;
}
